.loader .Loader-text {
    letter-spacing: .5em;
    font-size: .6em;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: pulse .9s ease infinite;
            animation: pulse .9s ease infinite;
}

@-webkit-keyframes pulse {
    50% {
        opacity: 0;
    }
}

@keyframes pulse {
    50% {
        opacity: 0;
    }
}


